
/* --- Custom Fonts (Ghost) --- */
/* Ghost injects: --gh-font-heading and --gh-font-body via {{ghost_head}} */
:root {
  /* Optional: your theme defaults as fallbacks */
  --theme-font-body: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  --theme-font-heading: ui-serif, Georgia, "Times New Roman", serif;
}

body {
  font-family: var(--gh-font-body, var(--theme-font-body));
}
h1, h2, h3, h4, h5, h6 {
  font-family: var(--gh-font-heading, var(--theme-font-heading));
}

/* Color scheme hooks driven by @custom.color_scheme */
.color-auto { color-scheme: light dark; }
.color-light { color-scheme: light; }
.color-dark  { color-scheme: dark; }


/* --- Koenig editor widths --- */
.kg-width-wide { /* content that should be wider than the text column */ }
.kg-width-full { /* content that should span the full viewport */ }

/* Typical layout helpers */
.content, .gh-container { max-width: 760px; margin: 0 auto; padding: 0 2rem; }
.kg-content-wide { max-width: 1080px; margin-left: auto; margin-right: auto; }

/* Make “wide” blocks wider than the article column */
.kg-width-wide,
.kg-gallery-card.kg-width-wide,
.kg-image-card.kg-width-wide {
  display: block;
  margin-left: calc(50% - 540px);
  margin-right: calc(50% - 540px);
  max-width: 1080px;
}

/* Make “full” blocks full-bleed */
.kg-width-full,
.kg-gallery-card.kg-width-full,
.kg-image-card.kg-width-full,
.kg-embed-card.kg-width-full {
  width: 100vw;
  max-width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
}

/* Sensible defaults for common Koenig cards */
.kg-card { margin: 2rem 0; }
.kg-image-card img { display: block; height: auto; width: 100%; }
.kg-gallery-container { display: grid; gap: 0.75rem; }
